import { notFound } from "next/navigation";
import Head from "next/head";
import Header from "@/components/Header";
import LeftSidebar from "@/components/LeftSidebar";
import RightSidebar from "@/components/RightSidebar";
import Footer from "@/components/Footer";
import { SanityContentRenderer } from "@/lib/sanityContent";
import { urlFor } from "@/lib/sanity";
import { format } from "date-fns";
import { Button } from "@/components/ui/button";
import { ArrowLeft, Eye } from "lucide-react";

// -----------------------------
// Fetch Blog Data (Server-side)
// -----------------------------
async function getBlogPost(slug: string) {
  const res = await fetch(
    `${process.env.NEXT_PUBLIC_BASE_URL}/api/blog/${slug}`,
    { cache: "no-store" }
  );

  if (!res.ok) return null;
  return await res.json();
}

// -----------------------------
// PAGE COMPONENT
// -----------------------------
export default async function BlogPage({ params }: { params: { slug: string } }) {
  const blogPost = await getBlogPost(params.slug);

  if (!blogPost) return notFound();

  const metaTitle = blogPost.seo?.metaTitle || blogPost.title;
  const metaDescription =
    blogPost.seo?.metaDescription ||
    blogPost.excerpt ||
    `Read ${blogPost.title} - Legal insights and analysis from Commercial Law Chamber`;

  const canonicalUrl = `https://www.clclaw.in/blog/${blogPost.slug.current}`;

  const ogImage = blogPost.featuredImage
    ? urlFor(blogPost.featuredImage).width(1200).height(630).fit("crop").url()
    : "https://www.clclaw.in/clc-logo.png";

  const publishedDate = blogPost.publishedAt
    ? new Date(blogPost.publishedAt).toISOString()
    : new Date().toISOString();

  return (
    <>
      {/* ------------------------- SEO ------------------------- */}
      <Head>
        <title>{metaTitle} | Commercial Law Chamber</title>
        <meta name="description" content={metaDescription} />
        <meta
          name="keywords"
          content={`${blogPost.category?.name || "legal"}, tax law, commercial disputes, legal insights, ${blogPost.title}`}
        />
        <meta name="author" content="Commercial Law Chamber" />
        <meta name="article:published_time" content={publishedDate} />

        {/* OG */}
        <meta property="og:title" content={metaTitle} />
        <meta property="og:description" content={metaDescription} />
        <meta property="og:type" content="article" />
        <meta property="og:url" content={canonicalUrl} />
        <meta property="og:image" content={ogImage} />

        {/* Twitter */}
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:title" content={metaTitle} />
        <meta name="twitter:image" content={ogImage} />

        <link rel="canonical" href={canonicalUrl} />

        {/* Structured Data */}
        <script
          type="application/ld+json"
          dangerouslySetInnerHTML={{
            __html: JSON.stringify({
              "@context": "https://schema.org",
              "@type": "BlogPosting",
              headline: blogPost.title,
              description: metaDescription,
              image: ogImage,
              datePublished: publishedDate,
              author: {
                "@type": "Organization",
                name: "Commercial Law Chamber",
              },
              publisher: {
                "@type": "LegalService",
                name: "Commercial Law Chamber",
                logo: {
                  "@type": "ImageObject",
                  url: "https://www.clclaw.in/clc-logo.png",
                },
              },
              mainEntityOfPage: {
                "@type": "WebPage",
                "@id": canonicalUrl,
              },
              articleSection: blogPost.category?.name || "Legal Insights",
              inLanguage: "en-IN",
            }),
          }}
        />
      </Head>

      {/* ---------------------- Page Layout ---------------------- */}
      <div className="min-h-screen">
        <div className="max-w-7xl bg-slate-900/40 mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <Header />

          <div className="flex flex-col lg:flex-row gap-4">
            {/* LEFT SIDEBAR */}
            <div className="hidden lg:block lg:w-48 xl:w-52">
              <LeftSidebar activeSection="insights" />
            </div>

            {/* CONTENT */}
            <div className="flex-1 min-w-0">
              <article className="bg-white/95 backdrop-blur-sm p-8">
                <div className="max-w-4xl">
                  {/* Back Button */}
                  <Button
                    variant="ghost"
                    onClick={() => history.back()}
                    className="flex items-center gap-2 text-gray-600 hover:text-gray-800 p-0 h-auto mb-6"
                  >
                    <ArrowLeft className="h-4 w-4" />
                    Back to Previous Page
                  </Button>

                  <h1 className="text-3xl lg:text-4xl font-bold poppins text-[#163C0F] mb-6">
                    {blogPost.title}
                  </h1>

                  {blogPost.category && (
                    <span className="inline-block bg-primary/10 text-primary px-3 py-1 rounded-full text-sm font-medium mb-4">
                      {blogPost.category.name}
                    </span>
                  )}

                  {blogPost.publishedAt && (
                    <time className="block text-sm text-gray-600 mb-6">
                      {format(new Date(blogPost.publishedAt), "MMMM d, yyyy")}
                    </time>
                  )}

                  {/* Featured Image */}
                  {blogPost.featuredImage && (
                    <div className="mb-8">
                      <img
                        src={urlFor(blogPost.featuredImage)
                          .width(800)
                          .height(400)
                          .fit("crop")
                          .url()}
                        alt={blogPost.title}
                        className="w-full rounded-lg shadow-md"
                      />
                    </div>
                  )}

                  {/* CONTENT BODY */}
                  <div className="text-gray-700">
                    <SanityContentRenderer content={blogPost.content} />
                  </div>

                  {/* Download Button */}
                  {blogPost.downloadUrl && (
                    <div className="flex items-center gap-3 mt-8">
                      <Button variant="outline" asChild>
                        <a
                          href={blogPost.downloadUrl}
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          <Eye className="h-4 w-4" />
                          Read The Full Document Here
                        </a>
                      </Button>
                    </div>
                  )}
                </div>
              </article>
            </div>

            {/* RIGHT SIDEBAR */}
            <div className="hidden lg:block lg:w-60 xl:w-64">
              <RightSidebar />
            </div>
          </div>

          <Footer />
        </div>
      </div>
    </>
  );
}
